
import { useState } from 'react';
import { NavLink } from 'react-router-dom';
import './sidebar.css';




const Sidebar = () => {

      const [expandCovid, setexpandCovid] = useState(false);
      const [expandDashboard, setexpandDash] = useState(false);
      const [expandPages, setexpandPages] = useState(false);
      const [expandProfile, setexpandProfile] = useState(false);
      const [sidebarhide, setsidebarhide] = useState(false)
      


      return (
            
            <div className={`sidebar ${sidebarhide ? "active" : ""}`}>
            
                  <div className="hidetoggle">
                  <div className={`hideshow_sidebar ${sidebarhide ? "active" : ""}`}>
                        <i className="fas fa-bars" onClick={() => setsidebarhide(!sidebarhide)}></i>
                  </div>  
                  </div>
                  
                  
                  <h4 className={`h4 ${sidebarhide ? "active" : ""} `}>Navigation</h4>
                  <div className={`dashboard ${sidebarhide ? "active" : ""}`} onClick={() => setexpandDash(!expandDashboard)} >

                 <i class="fas fa-home"></i><span className={`homeIcon ${sidebarhide ? "active" : ""}`} > Data 
                              <span  className={`expand_dash ${expandDashboard ? "active" : ""}`}><i className="fas fa-angle-down"></i></span></span>
                  </div>



                  <ul className={`covidList ${expandDashboard ? "active" : ""}`}>
                        <div className={`hideList ${sidebarhide ? "active" : ""}`}>

                              <li><NavLink to="/" className="sidelink"  ><i className="bi bi-chevron-right"></i><span className={`right ${sidebarhide ? "active" : ""}`}> Home</span></NavLink></li>
                              <li ><NavLink to="/country" className="sidelink" ><i className="bi bi-chevron-right"></i><span className={`right ${sidebarhide ? "active" : ""}`}> Countries</span></NavLink></li>
                              <li><NavLink to="/states" className="sidelink" ><i className="bi bi-chevron-right"></i><span className={`right ${sidebarhide ? "active" : ""}`}> State</span></NavLink></li>

                        </div>
                  </ul>



                  <div className={`Covid19 ${sidebarhide ? "active" : ""}`} onClick={() => setexpandCovid(!expandCovid)} >

                  <i class="fas fa-virus"></i><div className={`covidIcon ${sidebarhide ? "active" : ""}`} > Covid
                  <span  className={`expand_covid ${expandCovid ? "active" : ""}`}><i className="fas fa-angle-down"></i></span></div>
                  </div>


                  <ul className={`covidList ${expandCovid ? "active" : ""}`}>

                        <div className={`hideList ${sidebarhide ? "active" : ""}`}>


                              <li><NavLink to="/totalcases" className="sidelink"><i className="bi bi-chevron-right"></i><span className={`right ${sidebarhide ? "active" : ""}`}> Total Cases</span></NavLink></li>
                              <li><NavLink to="/activecases" className="sidelink"><i className="bi bi-chevron-right"></i><span className={`right ${sidebarhide ? "active" : ""}`}> Active Cases</span></NavLink></li>
                              <li><NavLink to="/recovered" className="sidelink"><i className="bi bi-chevron-right"></i><span className={`right ${sidebarhide ? "active" : ""}`}> Recovered</span></NavLink></li>
                              <li><NavLink to="/deaths" className="sidelink"><i className="bi bi-chevron-right"></i><span className={`right ${sidebarhide ? "active" : ""}`}> Deaths</span></NavLink></li>
                              <li><NavLink to="/analytics" className="sidelink"><i className="bi bi-chevron-right"></i><span className={`right ${sidebarhide ? "active" : ""}`}> Analytics</span></NavLink></li>
                        </div>

                  </ul>

                  <h4 className={`h4 ${sidebarhide ? "active" : ""} `} >Pages</h4>
                  <div className={`pages ${sidebarhide ? "active" : ""}`} onClick={() => setexpandPages(!expandPages)} >

                  <i class="fas fa-file"></i><div className={`PagesIcon ${sidebarhide ? "active" : ""}`}> Pages
                  <span  className={`expand_pages ${expandPages ? "active" : ""}`}><i className="fas fa-angle-down"></i></span></div>
                  </div>

                  <ul className={`covidList ${expandPages ? "active" : ""}`}>

                        <div className={`hideList ${sidebarhide ? "active" : ""}`}>
                              <div className={`innerhideList ${sidebarhide ? "active" : ""}`}>


                              <li><NavLink to="/login" className="sidelink"><i className="bi bi-chevron-right"></i><span className={`right ${sidebarhide ? "active" : ""}`}> Login</span></NavLink></li>
                              <li><NavLink to="/signup" className="sidelink"><i className="bi bi-chevron-right"></i><span className={`right ${sidebarhide ? "active" : ""}`}> Sign up</span></NavLink></li>


                              </div>
                        </div>

                  </ul>
                  <h4 className={`h4 ${sidebarhide ? "active" : ""} `}>Profile</h4>
                  <div className={`profile ${sidebarhide ? "active" : ""}`} onClick={() => setexpandProfile(!expandProfile)} >

                  <i class="fas fa-user"></i><span className={`ProfileIcon ${sidebarhide ? "active" : ""}`}> Profile
                  <span  className={`expand_profile ${expandDashboard ? "active" : ""}`}><i class="fas fa-angle-down"></i></span></span>
                  </div>

                  <ul className={`covidList ${expandProfile ? "active" : ""}`}>

                        <div className="sidebarFooter">

                        </div>
                  </ul>
            </div>

           
      )
}

export default Sidebar;