import SmallCard from '../../Cards/SmallCard';
import './home.css';
import { useEffect, useState } from 'react';
import axios from 'axios';



const Home = () => {
const [allcases, setallcases] = useState([])
const [allcases2, setallcases2] = useState([])
const [loading, setloading] = useState(false)
  const dataGet = async () => {


    const indiaTotalCases = 'https://api.apify.com/v2/key-value-stores/toDWvRj1JpTXiM8FF/records/LATEST?disableRedirect=true';
    const indiaTotalCases2 = ' https://corona.lmao.ninja/v2/countries/India, US, Pakistan, Russia, Australia, China ?yesterday=true&strict=true&query =';
   

    const allindiacasesData = await axios.get(indiaTotalCases)
    const allindiacasesData2 = await axios.get(indiaTotalCases2)

    setallcases(allindiacasesData.data);
    setallcases2(allindiacasesData2.data);

    setloading(true)

}

  useEffect(() => {

    dataGet()

  }, [])

  return (
  
    
    <div className="home_Container">
      <div id="updatedTime"><p>last updated at : <br/>{allcases.lastUpdatedAtApify}</p></div>
      {loading ? <> 
      <div className="firstContainer">
        <h3 id='covid'>COVID-19 TRACKER</h3>
        {/* {console.log(country)} */}
        {console.log(allcases)}
        {console.log(allcases2)}

              
      </div>


      <div className="smallCardsHolder">

        <SmallCard
          
          link="/totalCases"
          cases="Total Cases"
          caseNumber={allcases.totalCases}
          countryName={allcases.country}
          increase={allcases2[0].todayCases}
          icon={<i class="bi bi-arrow-up"></i>}
          incrStyle={{color:'rgb(184, 27, 27)',textAlign:'center',fontSize:'20px',fontWeight:'700'}}
          cardStyle={{color:'#44a1f8'}}
        />



        <SmallCard
         
          link="/totalCases"
          cases="Total Active"
          caseNumber={allcases.activeCases}
          countryName={allcases.country}
          increase={Math.abs(allcases.activeCasesNew)}
          icon={<i class="bi bi-arrow-down"></i>}
          incrStyle={{color:'green',textAlign:'center',fontSize:'20px',fontWeight:'700'}}
          cardStyle={{color:'#44a1f8'}}
        />
        <SmallCard
         
          link="/totalCases"
          cases="Total Recovered"
          caseNumber={allcases.recovered}
          countryName={allcases.country}
          icon={<i class="bi bi-arrow-up"></i>}
          increase={allcases.recoveredNew}
          incrStyle={{color:'green',textAlign:'center',fontSize:'20px',fontWeight:'700'}}
          cardStyle={{color:'green'}}


        />


        <SmallCard
          
          link="/totalCases"
          cases="Total Deaths"
          caseNumber={allcases.deaths}
          countryName={allcases.country}
          increase={allcases.deathsNew}
          icon={<i class="bi bi-arrow-up"></i>}
          incrStyle={{color:'rgb(184, 26, 26)',textAlign:'center',fontSize:'20px',fontWeight:'700'}}
          cardStyle={{color:'rgb(184, 27, 27)'}}
          />

        <SmallCard
         
          link="/totalCases"
          cases="Today Cases"
          countryName={allcases.country}
          caseNumber={allcases2[0].todayCases}
          cardStyle={{color:'#44a1f8'}}

         



        />

        <SmallCard
         
          link="/totalCases"
          cases="Today Deaths"
          countryName={allcases.country}
          caseNumber={allcases2[0].todayDeaths}
          incrStyle={{color:'rgb(184, 26, 26)',textAlign:'center',fontSize:'20px',fontWeight:'700'}}
          cardStyle={{color:'rgb(184, 27, 27)'}}

        />

      </div>

      <div className="thirdCardsHolder">
        <div className="safty">

          <img src="https://miro.medium.com/max/1400/1*FpiaRJz6Ii3XxYFWkx5IBg.gif" alt="not found" />


        </div>
        <div className="safty">

          <img src="https://www.cdc.gov/coronavirus/2019-ncov/images/homepage/splash-version/vaccine-booster-covid_618.png?_=96192" alt="not found" />
        </div>

      </div>
      </>
: <div id="loader">
<div className="loader">Loading...</div>
</div>}



    </div>
  )
}

export default Home;