import React from 'react'
import { NavLink } from 'react-router-dom'
import './smallCard.css'
const SmallCard = (props) => {
      return (

            <NavLink to={props.link} className="smallCard" style={props.cardStyle}>
                        <div >
                              <div className="img">
                                    <img src="https://media.giphy.com/media/wvP4x67EnIhWiCztAf/giphy.gif" alt="not found" />
                              </div>
                              <div id="india"><p style={props.cardStyle}>{props.countryName}</p></div>
                              <div className="cases" style={props.cardStyle}>{props.cases}</div>
                              <div className="numCases" style={props.cardStyle}>{props.caseNumber}</div>
                              <div style={props.incrStyle} >{props.increase}{props.icon}</div>



                  </div></NavLink>
      )
}

export default SmallCard;